<template>
  <div class="authority">
    <div class="button-box clearflex">
      <el-button @click="addAuthority('0')" type="primary">新增角色</el-button>
    </div>
    <el-table
            :data="tableData"
            style="width: 100%"
            row-key="ID"
            border
            stripe
            :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
        <el-table-column label="id" min-width="180" prop="ID"></el-table-column>
        <el-table-column label="角色id" min-width="180" prop="authorityId"></el-table-column>
        <el-table-column label="角色名称" min-width="180" prop="authorityName"></el-table-column>
        <el-table-column fixed="right" label="操作" width="500">
            <template slot-scope="scope">
                <el-button @click="opdendrawer(scope.row)" size="small" type="text">设置权限</el-button>
                <el-button @click="deleteAuth(scope.row)" size="small" type="text">删除角色</el-button>
                <el-button @click="addAuthority(scope.row.authorityId)" size="small" type="text">新增子角色</el-button>
            </template>
        </el-table-column>
    </el-table>

    <!-- 新增角色弹窗 -->
    <el-dialog :visible.sync="dialogFormVisible" title="新增角色">
      <el-form ref="form" :model="form">
         <el-form-item label="父级角色ID">
          <el-input autocomplete="off" disabled v-model="form.parentId"></el-input>
        </el-form-item>
        <el-form-item label="角色ID" prop="authorityId" :rules="[
            {required: true, message: '角色ID不能为空', trigger: 'blur'},
            {pattern: /^(?!0+$)^\d*$/, message: '只允许输入大于0的数字！'}
          ]">
          <el-input autocomplete="off" v-model="form.authorityId"></el-input>
        </el-form-item>
        <el-form-item label="角色姓名" prop="authorityName" :rules="[
            {required: true, message: '角色姓名不能为空', trigger: 'blur'},
          ]">
          <el-input autocomplete="off" v-model="form.authorityName"></el-input>
        </el-form-item>
      </el-form>
      <div class="dialog-footer" slot="footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button @click="enterDialog" type="primary">确 定</el-button>
      </div>
    </el-dialog>

    <el-drawer :visible.sync="drawer" v-if="drawer" :with-header="false" size="40%" title="角色配置">
      <el-tabs class="role-box" type="border-card">
        <el-tab-pane label="角色菜单">
          <Menus :row="activeRow" />
        </el-tab-pane>
        <el-tab-pane label="角色api">
          <apis :row="activeRow" />
        </el-tab-pane>
      </el-tabs>
    </el-drawer>
  </div>
</template>

<script>
// 获取列表内容封装在mixins内部  getTableData方法 初始化已封装完成
import {
  getAuthorityList,
  deleteAuthority,
  createAuthority
} from '@/api/authority'

import Menus from '@/views/superAdmin/authority/components/menus'
import Apis from '@/views/superAdmin/authority/components/apis'

import infoList from '@/components/mixins/infoList'
export default {
  name: 'Authority',
  mixins: [infoList],
  data () {
    return {
      listApi: getAuthorityList,
      listKey: 'list',
      drawer: false,
      activeRow: {},
      activeUserId: 0,
      dialogFormVisible: false,
      apiDialogFlag: false,
      form: {
        authorityId: '',
        authorityName: '',
        parentId: '0'
      }
    }
  },
  components: {
    Menus,
    Apis
  },
  methods: {
    opdendrawer (row) {
      this.drawer = true
      this.activeRow = row
    },
    // 删除角色
    deleteAuth (row) {
      this.$confirm('此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const res = await deleteAuthority({ authorityId: row.authorityId })
        if (res.success) {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getTableData()
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 初始化表单
    initForm () {
      for (const key in this.form) {
        this.form[key] = ''
      }
    },
    // 关闭窗口
    closeDialog () {
      this.initForm()
      this.dialogFormVisible = false
      this.apiDialogFlag = false
    },
    // 确定弹窗

    async enterDialog () {
      const res = await createAuthority(this.form)
      if (res.success) {
        this.$message({
          type: 'success',
          message: '添加成功!'
        })
        this.getTableData()
        this.closeDialog()
      }
      this.initForm()
      this.dialogFormVisible = false
    },
    // 增加角色
    addAuthority (parentId) {
      this.form.parentId = parentId
      this.dialogFormVisible = true
    }
  },
  created () {
    this.pageSize = 999
  }
}
</script>

<style lang="scss">
.authority {
  .button-box {
    padding: 10px 20px;
    .el-button {
      float: right;
    }
  }
}
.role-box {
    .el-tabs__content {
      height: calc(100vh - 150px);
      overflow: auto;
    }
  }
</style>
